body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.text-center{text-align:center}.nav{position:fixed;top:0px}.footer{background-color:#282c34;color:white;margin:0}.fullscreenItem{height:100vh;width:100%}.fullscreenContent{margin-top:25vh;color:grey}.aboutPage{width:100%;height:100%}.profilePic{width:260px;height:210px;margin:20px;border-radius:5%}p{font-size:1rem}.educationAccordion{width:70%;max-width:800px;margin-left:auto;margin-right:auto;margin-top:30px;color:grey}.educationAccordion .educationInfoGroup{text-align:left}.educationAccordion .educationInfoGroup .titleRow{font-size:1.5rem}.educationAccordion .educationInfoGroup .schoolRow{font-size:1.25rem}.educationAccordion .educationInfoGroup .courseRow p{font-size:1.25rem}.educationAccordion .educationInfoGroup .courseRow .courseList{font-size:1rem}.educationAccordion .educationInfoGroup .courseRow .courseList li{display:inline-block;margin:5px;padding:5px;border:lightblue 1px solid;border-radius:5px}@media only screen and (max-width: 768px){.fullscreenItem{height:auto;width:90%}.fullscreenContent{margin-top:40px;height:auto}.fullscreenContent:first-of-type{margin-top:80px}.fullscreenContent .jumbotron{height:auto}ul.courseList{padding-left:0%}ul.courseList li{text-align:center;width:100%}}

